/* src/components/Auth.css */

/* Center the form */
.auth-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  background: #f0f2f5;
}

/* Form container */
.auth-box {
  background: white;
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  width: 350px;
  text-align: center;
  margin: auto;
}

/* Form title */
.auth-box h2 {
  margin-bottom: 20px;
  color: #333;
}

/* Inputs */
.auth-box input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 14px;
}

/* Button */
.auth-box button {
  width: 100%;
  padding: 12px;
  border: none;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
}

.auth-box button:hover {
  background-color: #0056b3;
}


