/* src/App.css */

/* Reset */
body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f8f9fa;
  color: #333;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #0046d2;
  color: white;
  padding: 15px 30px;
}

.logo {
  margin: 0;
}

.nav-links a {
  margin-left: 20px;
  text-decoration: none;
  color: white;
  font-weight: 500;
}

.nav-links a.active {
  border-bottom: 2px solid yellow;
}

/* Page content */
.content {
  padding: 20px 40px;
}

/* Forms */
.form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin-top: 20px;
}

.form input {
  margin-bottom: 12px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
}

.form button {
  padding: 10px;
  background: #0056d2;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.form button:hover {
  background: #003f99;
}

/* Danger button */
.danger-btn {
  padding: 10px 20px;
  background: crimson;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.danger-btn:hover {
  background: darkred;
}

/* Responsive */
@media (max-width: 768px) {
  .nav-links {
    display: flex;
    flex-direction: column;
  }

  .nav-links a {
    margin: 10px 0;
  }
}
.form-container {
  max-width: 400px;
  margin: 20px auto;
  padding: 25px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.form input, 
.form select {
  width: 100%;
  padding: 10px;
  margin: 8px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.form .btn, .danger-btn {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
}

.btn {
  background: #4caf50;
  color: white;
}

.btn:hover {
  background: #45a049;
}

.danger-btn {
  background: #f44336;
  color: white;
}

.danger-btn:hover {
  background: #d32f2f;
}
.list-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 25px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 12px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

tr:hover {
  background: #f9f9f9;
}

.btn, .danger-btn {
  margin: 2px;
  padding: 6px 12px;
  border-radius: 6px;
  text-decoration: none;
  font-size: 14px;
}
.logout-btn {
  background: #ff4d4f;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
}

.logout-btn:hover {
  background: #d9363e;
}
