.adduser-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f2ceea;
}

.adduser-box {
  background: white;
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  width: 400px;
  text-align: center;
}

.adduser-box h2 {
  margin-bottom: 20px;
  color: #333;
}

.adduser-box form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.adduser-box input,
.adduser-box select {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  font-size: 14px;
}

.adduser-box input:focus,
.adduser-box select:focus {
  border-color: #007bff;
}

.adduser-box button {
  padding: 10px;
  background: #007bff;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
}

.adduser-box button:hover {
  background: #0056b3;
}
